import React from "react";
import Hero from "../components/Hero";
import Content from "../components/Content";

function AboutPage(props) {
  return (
    <div>
      <Hero title={props.title} />

      <Content>
        <p>
          Hello, my name is Prabesh. I'm a full stack engineer with experience
          in Express JS, Node JS, Oracle SQL, MongoDB, React and Flutter.
        </p>

        <p>
          My dream is to one day start my own business and become an
          entrepreneur (currently working on some ideas).
        </p>

        <p>
          I'm constantly learning new things. currently those things include
          gaining more experience with MongoDB, React, Express JS, and Node JS
        </p>

        <p>
          My latest project, Social Connect, is a Social Registration Portal for
          developers. You can check it out{" "}
          <a
            href="https://github.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            here
          </a>
          , or on the homepage. It is built with React JS, MongoDB, Express JS,
          and Node JS. However, I will be rebuilding it using flutter in the
          coming months
        </p>
      </Content>
    </div>
  );
}

export default AboutPage;
