import React from "react";

import profilePic from "../images/profile.png";
import facebook from "../images/fb.jpg";
import githubIcon from "../images/github-logo.jpg";

import Card from "../components/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

class Carousel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          title: "Portfolio Page",
          subTitle: "Made using React",
          imgSrc: profilePic,
          link: "https://praveas.github.io/react-deploy/",
          selected: false,
        },
        {
          id: 1,
          title: "Prabesh Pokharel",
          subTitle: "Facebook Page",
          imgSrc: facebook,
          link: "https://www.facebook.com/praveas",
          selected: false,
        },
        {
          id: 2,
          title: "Evverest",
          subTitle: "A social network for developers",
          imgSrc: githubIcon,
          link: "https://github.com/praveas",
          selected: false,
        },
      ],
    };
  }

  handleCardClick = (id, card) => {
    let items = [...this.state.items];

    items[id].selected = items[id].selected ? false : true;

    items.forEach((item) => {
      if (item.id !== id) {
        item.selected = false;
      }
    });

    this.setState({
      items,
    });
  };

  makeItems = (items) => {
    return items.map((item) => {
      return (
        <Card
          item={item}
          click={(e) => this.handleCardClick(item.id, e)}
          key={item.id}
        />
      );
    });
  };

  render() {
    return (
      <Container fluid={true}>
        <Row className="justify-content-around">
          {this.makeItems(this.state.items)}
        </Row>
      </Container>
    );
  }
}

export default Carousel;
